import React from 'react';
import { ORIGINAL_SVG_mailicon } from '../../../../../../../../../../../utils/defaults';
import generatesvgUrl from '../../../../../../../../../../../utils/generatesvgUrl';
const ReceipentsLogo: React.FC = (): JSX.Element => {
    const sideScroll = (
        element: HTMLDivElement,
        speed: number,
        distance: number,
        step: number
    ) => {
        let scrollAmount = 0;
        const slideTimer = setInterval(() => {
            element.scrollLeft += step;
            scrollAmount += Math.abs(step);
            if (scrollAmount >= distance) {
                clearInterval(slideTimer);
            }
        }, speed);
    };
    const contentWrapper = React.useRef(null);
    const svg = generatesvgUrl(ORIGINAL_SVG_mailicon)
    return (
        <div className="h-full flex w-full  flex-col">
            <div className=" w-full px-4">
                <h1 className="mt-10 font-bold text-xl ">Your Brand Logo</h1>
            </div>
            <div className="flex flex-row mt-4   gap-2 overflow-x-scroll  items-center overflow-hidden  no-scrollbar"  >
                <div>
                    <button className=" pl-2 mt-3" onClick={() => {
                        sideScroll(contentWrapper.current, 25, 100, -10);
                    }}><svg width="26" height="26" fill="#D1D5DB" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                            <path d="M9 13L6 10M6 10L9 7M6 10L14 10M1 10C1 5.02944 5.02944 1 10 1C14.9706 1 19 5.02944 19 10C19 14.9706 14.9706 19 10 19C5.02944 19 1 14.9706 1 10Z" stroke="#ffffff" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" />
                        </svg>
                    </button>
                </div>
                <div className=" h-28 z-50 -mr-6 bg-gradient-to-r w-6 from-white  bg-opacity-80 to-transparent"></div>
                <div className=" mt-4 flex flex-row flex-shrink-0 px-1 gap-3 flex-grow w-72 overflow-x-scroll  items-center overflow-hidden relative no-scrollbar" ref={contentWrapper}>
                    <button className="flex flex-col justify-center items-center w-20 h-16 border-dashed border-2 rounded-md border-bordercolor flex-shrink-0">
                        <svg className="bg-indigo400 rounded-full" width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M11 5V11M11 11V17M11 11H17M11 11L5 11" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" />
                        </svg>
                        <p>Add new</p>
                    </button>
                    <button>
                        <svg width="84" height="72" viewBox="0 0 84 72" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                            <rect width="84" height="72" fill="url(#pattern0)" />
                            <defs>
                                <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
                                    <use xlinkHref="#image0" transform="translate(0 -0.00400798) scale(0.000244141)" />
                                </pattern>
                                <image id="image0" width="4096" height="3539" xlinkHref="data:image/png;base64," />
                            </defs>
                        </svg>
                    </button>
                    <button className="flex-shrink-0"> <img src={svg} className="w-12 flex-shrink-0" /></button>
                    <button className="flex-shrink-0"> <img src={svg} className="w-12 flex-shrink-0" /></button>
                    <button className="flex-shrink-0"> <img src={svg} className="w-12 flex-shrink-0" /></button>
                    <button className="flex-shrink-0"> <img src={svg} className="w-12 flex-shrink-0" /></button>
                    <button>
                        <svg width="83" height="72" viewBox="0 0 83 72" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                            <rect width="83" height="72" fill="url(#pattern0)" />
                            <defs>
                                <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
                                    <use xlinkHref="#image0" transform="translate(-0.00200009) scale(0.000245117)" />
                                </pattern>
                                <image id="image0" width="4096" height="3539" xlinkHref="data:image/png;base64," />
                            </defs>
                        </svg>
                    </button>
                    <button>
                        <svg width="83" height="72" viewBox="0 0 83 72" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                            <rect width="83" height="72" fill="url(#pattern0)" />
                            <defs>
                                <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
                                    <use xlinkHref="#image0" transform="translate(-0.00200009) scale(0.000245117)" />
                                </pattern>
                                <image id="image0" width="4096" height="3539" xlinkHref="data:image/png;base64," />
                            </defs>
                        </svg>
                    </button>
                </div>
                <div className=" h-28 z-50 -ml-8 bg-gradient-to-l w-8 from-white  bg-opacity-50 to-transparent"></div>
                <div className="">
                    <button className="pr-2  mt-3 " onClick={() => {
                        sideScroll(contentWrapper.current, 25, 100, 10);
                    }}><svg width="26" height="26" fill="#D1D5DB" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                            <path d="M11 7L14 10M14 10L11 13M14 10L6 10M19 10C19 14.9706 14.9706 19 10 19C5.02944 19 1 14.9706 1 10C1 5.02944 5.02944 1 10 1C14.9706 1 19 5.02944 19 10Z" stroke="#ffffff" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" />
                        </svg>
                    </button>
                </div>
            </div>
        </div >
    )
};
export default ReceipentsLogo;
